#root{
    height: 100vh;
}
p,a,div{
    font-family: Roboto,Helvetica Neue,Arial,sans-serif;
}
.form-group{
  position: relative;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.form-group__label{
  font-size: 12px;
    margin-bottom: 5px;
    text-transform: uppercase;
    display: block;
    width: 70px;

}

.combo-box__input{
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  color: #565656;
  width: calc(100% - 60px);
  height: 40px;

}
.combo-box__list{
  position: absolute;
  z-index: 10;
  background-color: white;
  list-style: none;
  width: 300px;
  margin: 0px;
  padding: 0px;
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  color: #565656;
  
}
.combo-box__list-item{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 10px;
 
}

.combo-box__list-item > input{
  margin-right: 10px;

}


input[text] {
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  color: #565656;
  width: 100%;
  height: 40px;
}

.input__input{
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  color: #565656;
  width: 100%;
  height: 40px;
  padding: 5px 10px 5px 10px;
}

input:focus {
  box-shadow: none;
  outline: none;
}
.button {
  color: #fff;
  background-color: #447df7;
  width: 100%;
  border: solid 1px #447df7;
  font-weight: 400;
  padding: 8px 16px;
  border-radius: 5px;
  outline: none;
}

.button--simulator{
  color: #fff;
  background-color: #447df7;
  border: solid 1px #447df7;
  font-weight: 400;
  padding: 4px 8px;
  position: fixed;
  top: 93px;
  right: 27px;
  width: 137px;
  height: 52px;
  border-radius: 121px;
  z-index: 3;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.5), 0 6px 20px 0 rgba(0,0,0,0.4);
  outline: none;
}
.select__list {
  width: 100%;
  border: 1px solid #e3e3e3;
    border-radius: 4px;
    color: #565656;
    width: 100%;
    height: 40px;
    background-color: white;
    padding: 4px;
    height: 50px;
}

.menu{
    background-color: #353536;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    padding-bottom: 20px; 
    width: 260px;
    box-shadow: inset -1px 0 0 0 #cfcfca; 
    height: calc(100% - 40px); 
}

.menu  ul  {
    list-style: none;
    color: white;
    margin: 0px;
    padding: 10px;
}

.menu  li  {
    color: white;
    height: 30px;
    padding: 5px;
}
.menu li:hover  {
    opacity: 1;
    background: hsla(0,0%,100%,.23);
}

.menu  a  {
    color: white;
    text-decoration: none;
    text-transform: uppercase;
    line-height: 30px;
}

.white-space{
    height: 10px;
    width: 100%;
}

.card{
    border-radius: 4px;
    box-shadow: 0 1px 2px rgba(0,0,0,.05), 0 0 0 1px rgba(63,63,68,.1);
    background-color: #fff;
    margin-bottom: 10px;
}

.card__header{
    padding: 15px 15px 0;
    
}
.card__header__title{
    margin: 0;
    color: #333;
    font-weight: 300;
    font-weight: bold;
}
.card__content{
    padding: 15px 15px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}


.card-item{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: calc(20% - 24px);
    margin: 6px;
    padding: 6px;
    border-radius: 4px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(63, 63, 68, 0.1);
    background-color: #fff;
    margin-bottom: 30px;
}

@media only screen and (max-width: 1024px) {
    .card-item {
        width: calc(33.33% - 24px);
    }
  }
@media only screen and (max-width: 768px) {
    .card-item {
        width: calc(50% - 24px);
    }
  }
@media only screen and (max-width: 600px) {
    .card-item {
        width: 100%
    }
  }


.card-item-row{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 5px;
}

.card-item-row-label
{
    white-space: nowrap;
}
.card-item-row-value
{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.status {
    height: 10px;
    width: 10px;
    margin-right: 7px;
    border-radius: 50%;
    display: inline-block;
  }

.status--connected {
    background-color: rgb(2, 185, 87);
}
.status--disconnected {
    background-color: rgb(194, 55, 55);
}

.header{
    position: fixed;
    background-color:#353536;
    color: white;
    left: 0px;
    right: 0px;
    width: 100%;
    margin: 0px;
    z-index: 1;
}

.header__title{
    margin-left: 10px;
}
.container {
    position: relative;
    top: 80px;
    padding: 10px;
}
.login__container{
    width: 400px;
    margin: 40px auto;
}

